function getShop() {
	let shops = []
	for (let i = 0; i < names.length; i++) {
		shops.push(
			{
				name: names[i],
				description: descs[i],
				imgsrc: images[i],
			}
		)
	}
	return shops;
}

function Recommendation({shop}) {
	const {name, description, imgsrc} = shop;
	return (
		<Card style={{ 'maxHeight': '20vh', 'overflow': 'hidden' }}>
			<Card.Img variant="top" src={imgsrc}/>
			<Card.ImgOverlay>
				<Card.Title>{name}</Card.Title>
				<Card.Text>
					{description}
				</Card.Text>
			</Card.ImgOverlay>
		</Card>
	);
}

